import React, {useState, Suspense, lazy} from "react"
import './styles/styles.scss'
import Header from './components/Header'
import Hero from "./components/Hero"

const Menu = lazy(() => import("./components/Menu"))
const About = lazy(() => import("./components/About"))
const Projects = lazy(() => import("./components/Projects"))
const Contact = lazy(() => import("./components/Contact"))
const Footer = lazy(() => import("./components/Footer"))

function App() {
  const [menuActive, setMenuActive] = useState(false)
  const onClickMenuLink = () => setMenuActive(!menuActive)

  const EMAIL = "remi.martineau.dev@gmail.com";

  return (
    <div className="App">
      <div className={`menu__activation-anim-elt ${menuActive? "menu__activation-anim-elt--active" : ""}`}></div>
      <Suspense fallback={<div></div>}>
        <Menu menuActive={menuActive} onClickMenuLink={onClickMenuLink}/>
      </Suspense>
      
      <Header menuActive={menuActive} setMenuActive={setMenuActive}/>
      <Hero />

      <Suspense fallback={<div/>}>
        <About />
      </Suspense>
      
      <Suspense fallback={<div/>}>
        <Projects />
      </Suspense>

      <Suspense fallback={<div/>}>
        <Contact email={EMAIL}/>
      </Suspense>

      <Suspense fallback={<div/>}>
        <Footer />
      </Suspense>
    </div>
  );
}

export default App;